import { useEffect, useState } from "react"
import { useLocation } from "react-router-dom";
import CryptoJS from 'crypto-js'
import { Button, Modal, Space } from "antd";
import { CheckCircleTwoTone } from "@ant-design/icons";
import './Detail.css';
const Detail = () => {
  const location = useLocation();
  const [data, setData] = useState();
  const [visible, setVisible] = useState(false)
  useEffect(() => {
    const encrypted = location.search.split('data=')[1]
    setData(decrypt(encrypted))
    // console.log(decrypt(encrypted), location);
  }, [location])

  useEffect(() => {
    if (data?.isTermination === 2) {
      document.title = ''
    } else {
      document.title = ''
    }
  }, [data])

  const decrypt = (data) => {
    const bytes = CryptoJS.AES.decrypt(data, '123dk5');
    const decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
    return decryptedData
  }
  return (
    <div className="detail">
      <div className="box">
        <Space direction="vertical">
          <h1>{data?.isTermination === 2 ? '' : ''}</h1>
          <div></div>
          <div>{data?.name || 'xxx'}</div>
          <div>{data?.IdCard || 'xxx'}</div>
          <div>{data?.price || '1'}</div>
          {
            data?.isTermination === 2 ? (
              <div className='content'>
                 <b></b>  <b>{data?.name || 'xxx'}</b>  
                
                
              </div>
            ) : (
              <div className='content'>
                {data?.txt}
              </div>
            )
          }
        </Space>
      </div>
      {
        data?.isTermination === 2 ? (
          <Button
            className="btn"
            danger
            size="large" type='primary'
            onClick={() => {
              setVisible(true)
            }}>
            
          </Button>
        ) : (
          <Button
            className="btn"
            size="large" type='primary'
            onClick={() => {
              setVisible(true)
            }}
          >
            
          </Button>
        )
      }
      <Modal
        open={visible}
        closable={false}
        maskClosable={false}
        centered
        keyboard={false}
        title=''
        footer={null}
      >
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <CheckCircleTwoTone style={{ fontSize: '32px', marginRight: 10 }} twoToneColor="#52c41a" /> <span style={{ fontSize: '20px' }}>{data?.isTermination === 2 ? '' : ''}</span>
        </div>
      </Modal>
    </div>
  )
}

export default Detail